.sidebar {
  width: 25%;
  background-color: #181818;
  color: white;
  padding: 20px;
  height: 100vh;
}

.sidebar-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 10px;
  border-bottom: 1px solid #333;
}

.profile-img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.header-icons i {
  margin-left: 15px;
  font-size: 20px;
}

.sidebar-search input {
  width: 100%;
  background: #333;
  border: none;
  padding: 10px;
  border-radius: 20px;
  color: white;
}

.sidebar-menu {
  margin-top: 20px;
}

.menu-heading {
  font-size: 14px;
  color: #999;
}

.contact-list {
  list-style: none;
  padding: 0;
}

.contact-item {
  display: flex;
  align-items: center;
  margin-top: 15px;
}

.contact-item img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.contact-item h4 {
  margin-left: 10px;
}

.contact-item span {
  margin-left: 10px;
  color: #666;
}
