.message {
    display: flex;
  }
  
  .sent {
    justify-content: flex-end;
  }
  
  .received {
    justify-content: flex-start;
  }
  
  .message-content {
    background-color: #222;
    padding: 10px 15px;
    border-radius: 20px;
    max-width: 60%;
    position: relative;
  }
  
  .message-content p {
    margin: 0;
  }
  
  .message-time {
    font-size: 12px;
    color: #999;
    position: absolute;
    bottom: -15px;
    right: 10px;
  }
  