.chat-input {
    padding: 10px 20px;
    border-top: 1px solid #333;
  }
  
  .chat-input form {
    display: flex;
    align-items: center;
  }
  
  .chat-input input {
    flex-grow: 1;
    background-color: #222;
    border: none;
    padding: 10px;
    border-radius: 30px;
    color: white;
  }
  
  .chat-input button {
    background-color: #f05454;
    border: none;
    border-radius: 50%;
    padding: 10px;
    margin-left: 10px;
    color: white;
  }
  
  .chat-input button i {
    font-size: 20px;
  }
  