.chat {
  width: 75%;
  background-color: #101010;
  color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
}

.chat-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #333;
  padding-bottom: 10px;
}

.contact-img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}

.chat-icons i {
  margin-left: 15px;
  font-size: 20px;
}

.chat-messages {
  flex-grow: 1;
  padding: 20px 0;
  overflow-y: auto;
}

.message {
  margin-bottom: 15px;
}
